"use client"
import { useEffect, useState } from 'react'
import CategoryTabs from './components/CategoryTabs'
import SubCategoryTabs from './components/SubCategoryTabs'
import RestaurantCard from './components/RestaurantCard'
import { getProfessionals } from './lib/getProfessionals'

export default function HomePage() {
  const [category, setCategory] = useState('')
  const [subCategory, setSubCategory] = useState('')
  const [items, setItems] = useState([])
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')

  useEffect(() => {
    if (!category || !subCategory) return

    const run = async () => {
      setLoading(true); setError('')
      try {
        const data = await getProfessionals({ category, subCategory })
        setItems(data.items || [])
      } catch (e) {
        setError(e.message || 'Failed to load')
        setItems([])
      } finally {
        setLoading(false)
      }
    }
    run()
  }, [category, subCategory])

  return (
    <main className="min-h-screen bg-white pb-16">
      <CategoryTabs value={category} onChange={(val) => {
        setCategory(val)
        setSubCategory('')
      }} />

      {category && (
        <SubCategoryTabs
          value={subCategory}
          onChange={setSubCategory}
          category={category}
        />
      )}

      {subCategory && (
        <>
          <p className="px-4 mt-4 text-[11px] font-medium text-gray-400 tracking-wide uppercase">
            {loading ? 'Loading…' : `${items.length} Professionals Available`}
          </p>
          {error && <p className="px-4 mt-2 text-sm text-red-600">{error}</p>}

          <div className="mt-2">
            {items.map((p) => (
              <RestaurantCard
                key={p.id}
                image={p.imageUrl || '/placeholder.png'}
                name={p.name}
              />
            ))}
          </div>
        </>
      )}
    </main>
  )
}
